<template>
  <label class="form-group">
    <button class="form-group__button" :name="name" :value="value">
      <slot>{{ label }}</slot>
    </button>
  </label>
</template>

<script>
export default {
  props: {
    name: {
      type: String
    },
    value: {
      type: String
    },
    label: {
      type: String
    }
  }
}
</script>

<style scoped>
  .form-group {
    display: block;
  }
  .form-group__button {
    background: none;
    border: none;
    outline: none;
    overflow: hidden;
    position: relative;
    z-index: 1;
    text-decoration: none;
    color: #fff;
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: bold;
    background: hsl(var(--ui-active));
    cursor: pointer;
    box-shadow: 0 1px 1px rgba(0,0,0,.1), 0 2px 2px rgba(0,0,0,.1);
    transition: background .2s;
  }
  .form-group__button:hover {
    background: hsl(var(--ui-active-h), var(--ui-active-s), calc(var(--ui-active-l) - 10%));
  }
</style>